//JS getDay
export enum Weekday{
    Sunday = 0,
    Monday = 1,
    Tuesday = 2,
    Wednesday = 3,
    Thursday = 4,
    Friday = 5,
    Saturday = 6,
};
export function getDayDescription(dayIndex :number) :string{
    switch(dayIndex){
        case 0: return "";
        case 1: return "";
        case 2: return "";
        case 3: return "";
        case 4: return "";
        case 5: return "";
        case 6: return "";
        default: throw new Error(dayIndex + "");
    }
}
export enum WeekStartDay{
    Sunday = "",
    Monday = ""
};
